<template>
  <div class="bs-docs-section" id="message">
    <h3 class="page-header">
      <a href="#message" class="anchor">Message </a>
    </h3>
    <div class="bs-example" >
      <button class="btn btn-success btn-lg"
        @click="showRight = !showRight">
        
      </button>
      <button class="btn btn-success btn-lg"
        @click="showMessage">
        
      </button>

      <button class="btn btn-danger btn-lg"
        @click="showTop = !showTop">
        
      </button>
      <hr>
      <message type="success" >
        
      </message>

      <message type="info" >
        
      </message>

      <message type="danger" dismissable>
        ,
      </message>

      <message type="warning" dismissable>
        ,
      </message>

      <message type="help" dismissable>
        ,
      </message>

      <message
        :show.sync="showRight"
        :duration="3000"
        type="success"
        width="400px"
        placement="top-right"
        dismissable
      >
        <span class="icon-ok-circled message-icon-float-left"></span>
        <p></p>
      </message>

      <message
        :show.sync="showTop"
        :duration="3000"
        type="danger"
        width="400px"
        placement="top"
        dismissable>
        <span class="icon-info-circled message-icon-float-left"></span>
        <p></p>
      </message>
    </div>

    <pre><code class="language-markup"><script type="language-mark-up">
<button class="btn btn-default btn-lg"
  @click="showRight = !showRight">
  

<button class="btn btn-default btn-lg"
  @click="showTop = !showTop">
  
</button>
<hr>
<message type="success" >
  
</message>

<message type="info" >
  
</message>

<message type="danger" dismissable>
  
</message>

<message type="warning" dismissable>
  
</message>

<message type="help" dismissable>
  
</message>

<message
  show="{{showRight}}"
  duration="3000"
  type="success"
  width="400px"
  placement="top-right"
  dismissable>
  <span class="icon-ok-circled message-icon-float-left"></span>
  <strong>Well Done!</strong>
  <p>.</p>
</message>

<message
  show="{{showTop}}"
  duration="3000"
  type="info"
  width="400px"
  placement="top"
  dismissable>
  <span class="icon-info-circled message-icon-float-left"></span>
  <strong>Heads up!</strong>
  <p></p>
</message>

</script></code></pre>

    <h2>Options</h2>
    <table class="table table-bordered">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>show</td>
          <td><code>Boolean</code></td>
          <td><code>true</code></td>
          <td></td>
        </tr>
        <tr>
          <td>dismissable</td>
          <td><code>Boolean</code></td>
          <td><code>false</code></td>
          <td></td>
        </tr>
        <tr>
          <td>type</td>
          <td><code>String</code>, one of
          <code>success</code>
          <code>info</code>
          <code>warning</code>
          <code>danger</code>
          <code>help</code>
          </td>
          <td><code>success</code></td>
          <td></td>
        </tr>
        <tr>
          <td>duration</td>
          <td><code>Number</code></td>
          <td><code>0</code></td>
          <td>.  <code>0</code>.</td>
        </tr>
        <tr>
          <td>width</td>
          <td><code>String</code></td>
          <td></td>
        </tr>
        <tr>
          <td>placement</td>
          <td><code>String</code>. one of <code>top</code>, <code>top-right</code></td>
          <td></td>
          <td></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import Message from 'src/components/Message/'
  export default {
    data() {
      return {
        showRight: false,
        showTop: false
      }
    },
    components: {
      Message,
    },
    methods:{
      showMessage() {
        Message.success('')
      }
    }
  }
</script>

<style>
  .message-icon-float-left {
    font-size:32px;float:left;margin-right:5px;
  }
</style>



